/* Inhabitent Journal */

.journal-post-page {
  display: flex;
  justify-content: space-between;
  // margin-left: 0;
  // margin-right: 0;
  //margin-bottom: 0;
}

.post-img {
  height: 5rem;
}

.journal-post-info {
  padding: .75rem;
  border: $border-solid;
  border-top-width: 5px; 

  p.commented-dates {
    font-size: .75rem;
  }

  a.post-title {
    text-decoration: none;
    text-transform: uppercase;
    font-family: $font_secondary_wide;
    font-weight: 600;
    font-style: normal;
    color: $color_green;
    font-size: 1.3em;
  }  

  a.read-more {
    text-decoration: none;
    text-transform: uppercase;
    padding: 11px 20px;
    border: 0.5px solid black;
    font-family: $font_secondary_norm;
    font-weight: normal;
    font-style: normal;
    color: $color_purple;
    position: absolute;
  }
}

.blog .site-content {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  width: 1120px;
}

.blog .content-area {
  border-right: $border-solid;
  padding-right: 5%;
  width: 1120px;
  display: flex;
  margin: 0 auto;
}

h2.widget-title {
  font-family: $font_secondary_wide;  
  font-weight: 600;
  font-style: normal;
  font-size: 1.5em;
  letter-spacing: 2.5px;
  color: $color_purple;

    span.day-of-the-week {
      font-family: $font_main;
      font-weight: bold;
      font-style: normal;
    }  
}

.blog,
.type-post .entry-header{
  position: relative;
}
.type-post .entry-title {
  position: absolute;
  top: 0;
}
.type-post .entry-meta {
  position: absolute;
  bottom: 0;
  right: 0;
}

